/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";

/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';

/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";

input{
    width: 70vw;
    height: 10vw;
    border: 1px solid gray;
    background-color: white;
    color: black;
    border-radius: 4px;
    transition: all .1s ease;
    &:focus{
        outline: none;
    }
}

.small{
    font-size: 16px;
}

ion-toolbar{
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25)
}

ion-toolbar:last-of-type{
    --border-width: 0px!important;
}

header{
    padding: 0.5%;
    display: flex;
    justify-content: space-between;
}

.atHeader, .atFooter{
    background-color: white;
}

.navBar{
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    padding: 2px;
    border: none;
}

a{
    text-decoration: none;
}

.logo > img{
    width: 25%;
}

.Login{
    color: black;
    border: 1px solid black;
    border-radius: 50px;
    height: 45px;
    width: 45px;
    align-self: flex-end;
    position: relative;
    p{
        margin: 0;
        text-align: center;
        white-space: nowrap;    
    }
}

.center{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
}

.backBtn{
    float: left;
    margin-top: -5vh;
    .goBack{
        color: lightgray;
    }
}

.title{
    text-align: center;
    color: black;
    font-weight: 600;
}

.bg{
    position: absolute;
    bottom: 0;
    width: 100vw;
    opacity: 25%;
    margin: 0 0 0 16vw;
}

.status{
    width: 50vw;
    margin: 0 auto;
    margin-top: 12vh;
}
.message{
    text-align: center;
    margin-bottom: 5vh;
    h1{
        font-size: 48px;
        font-weight: 550;
    }
    span{
        color: #FA744A;
    }
}
.actionBtn{
    display: flex;
    ion-button{
        width: 70vw;
        margin: 0 auto;
        color: black;
        font-size: 20px;
        font-weight: bolder;
        --background: #FFC107;
        --background-activated: #ffa807;
    }
}
.adIMG{
    border-radius: 8px;
    img{
        object-fit: cover;
    }
}

.text1{
    color: red;
    font-size: large;
    font-weight:bolder;
}

.text2{
    color:black;
    font-size: large;
    font-weight:900px;
}

.close{
    width: 34px;
    height: 34px;
    border-radius: 50px;
    background-color: #FFC107;
    font-size: 1.5rem;
    color: black;
    position: absolute;
    z-index: 2;
    margin: 18.5vh 0 0 88vw;
}

ion-card{
    padding: 3% 5%;
    background-color: white;
    width: 85vw;
    margin: 0 auto;
}

.dotSwitch{
    position: relative;
    left: 30%;
}

.login {
    width: 12em;
    height: 2.5em;
    font-size: 14pt;
    --background: #01A0C6;
  }

.modalOpen::part(scroll){
    overflow-y: hidden;
}

.modalClose::part(scroll){
    overflow-y: scroll;
}

.askModal{
    display: none;
    width: 100vw;
    height: 100vh;
    background-color: rgb(112 95 95 / 75%);
    position: absolute;
    z-index: 3;
    .askWindow{
        background-color: white;
        border-radius: 8px;
        padding: 2%;
        text-align: center;
        .close{
            margin: -3vh 0 0 32vw;
        }
    }
}

.notLogIn{
    display: block;
}

.closeCall{
    display: none;
}

#myModal{
    position: absolute;
    width: 100vw;
    z-index: 10;
    transform: translateY(-12vh);
    transition: all .5s ease;
    .modal-content {
        border: 1px solid #fa744a;
        background-color: #ffffff;
        margin: auto;
        padding: 10px;
        width: 70%;
        color: black;
        border-radius: 8px;
        text-align: center;
        line-height: 4px;
        transition: all .5s ease;
    }
  }

  ul{
    padding-left: 3vw;
      li{
        color: black;
        border: 1px solid white;
        background-color: white;
        border-radius: 8px;
        list-style: none;
        .thumbnail{
            img{
                width: 161px;
                height: 161px;
                object-fit: cover;
                border-radius: 8px;
            }
        }
      }
  }

  .price, .loggedIn{
    color: #FA744A;
}

#warning {
    font-size: 14pt;
    color: red;
    visibility: hidden;
    p{
      margin: 1% 0;
      padding: 1%;
    }
  }

  .form-inline {
    margin-top: 10vh;
    text-align: center;
    position: relative;
    z-index: 2;
  }